import React, { useRef, useState, useCallback } from 'react'
import { useTransition, animated } from 'react-spring'
import { Canvas, useFrame } from 'react-three-fiber'



function Box(props) {
    // This reference will give us direct access to the mesh
    const mesh = useRef()
  
    // Set up state for the hovered and active state
    const [hovered, setHover] = useState(false)
    const [active, setActive] = useState(false)
  
    let speed = 0.01;
    // Rotate mesh every frame, this is outside of React without overhead
    useFrame(function(){
      mesh.current.rotation.x = mesh.current.rotation.y += 0.01;
     
  
     

    } )
  
    return (
      <mesh
        {...props}
        ref={mesh}
        scale={active ? [1.5, 1.5, 1.5] : [1, 1, 1]}
        onClick={(e) => setActive(!active)}
        onPointerOver={(e) => setHover(true)}
        onPointerOut={(e) => setHover(false)}>
        <boxBufferGeometry attach="geometry" args={[1, 1, 1]} />
        <meshStandardMaterial attach="material" color={hovered ? 'purple' : 'orange'} />
      </mesh>
    )
  }

  function Locks(props) {
    // This reference will give us direct access to the mesh
    const mesh = useRef()
  
    // Set up state for the hovered and active state
    const [hovered, setHover] = useState(false)
    const [active, setActive] = useState(false)
  
    // Rotate mesh every frame, this is outside of React without overhead
    useFrame(() => (mesh.current.rotation.x = mesh.current.rotation.y += 0.01))
  
    return (
      <mesh
        {...props}
        ref={mesh}
        scale={active ? [1.5, 1.5, 1.5] : [1, 1, 1]}
        onClick={(e) => setActive(!active)}
        onPointerOver={(e) => setHover(true)}
        onPointerOut={(e) => setHover(false)}>
        <sphereBufferGeometry attach="geometry" args={[1, 32, 32]} />
        <meshStandardMaterial attach="material" color={hovered ? 'green' : 'purple'} />
      </mesh>
    )
  }


  const pages = [
    ({ style }) => <animated.div className="messageBox" style={{ ...style, background:'#9B2915'}}><span className='message'>Click the cube</span></animated.div> ,
    ({ style }) => <animated.div className="messageBox" style={{ ...style, background: '#9B2915' }}>B</animated.div>,
    ({ style }) => <animated.div className="messageBox" style={{ ...style, background: '#9B2915' }}>C</animated.div>,
    ({ style }) => <animated.div className="messageBox" style={{ ...style, background: '#9B2915' }}>D</animated.div>,
  ]


function Images () {


  
  const [locked, setLock] = useState(false)

  const [index, set] = useState(0)
  const onClick = useCallback(() => set(state => (state + 1) % 4), [])
  const messagebox = useTransition(index, p => p, {
    from: { opacity: 0, transform: 'translate3d(-100%, 0, 0)' },
    enter: { opacity: 1, transform: 'translate3d(0%, 0, 0)' },
    leave: { opacity: 0, transform: 'translate3d(150%, 0, 0)' },
  })



    return (
        <div className="imageWrapper">
            <div id="background-image" className="background-image">
              <div className="uiwrapper">
            
            
              <Canvas>
              <ambientLight />
                  <pointLight position={[-10, 10, 10]} />
                  <Box position={[0, 0, 0]} />
        
              </Canvas>
              <div className="pop-up" onClick={onClick}>
                  {messagebox.map(({ item, props, key }) => {
                  
                        const Page = pages[item]
                    
                  
                    return <Page key={key} style={props} />
                  })}
              </div>
              </div>
            </div>
      
          
        </div>
    )
}

export default Images;

