import React, {useState} from 'react';
import {useSpring, animated} from 'react-spring'

const calc = (x, y) => [-(y - window.innerHeight / 2) / 2000, (x - window.innerWidth / 2) / 2000, 0.75]
const trans = (x, y, s) => `perspective(800px) rotateX(${x}deg) rotateY(${y}deg) scale(${s})`

function LandPad () {

    const [clicked, setClicked] = useState(false)
    const [color, setColor] = useState('black')
    const [props, set] = useSpring(() => ({ xys: [0, 0, 1], config: { mass: 5, tension: 600, friction: 70 } }))

    return (
        <animated.div onClick={({ clientX: x, clientY: y }) => set({ xys: calc(x, y) })}
        
        style={{ transform: props.xys.interpolate(trans) }}>
            <div onClick={function(){  
                
                if (Math.random()*4 < 2 && clicked === false) {
                    console.log("ping")
                  setColor('blue')
                  setClicked(true);
                  return 0

                }
                if (Math.random()*4 > 2 && clicked === false) {
                    console.log("pong")
                  setColor('green')
                  setClicked(true);
                  return 0
                }
                if (clicked === false) {
                    console.log("boom")
                  setColor('red')
                  setClicked(true);
                  return 0

                }
            }} className='land-pad-square' style={{background: color, height: '70px',width: '70px'} }>
                    {color}
            </div>
        </animated.div>
        
    )
}

export default LandPad;
