import React from 'react';
import {useSpring, animated} from 'react-spring'

const calc = (x, y) => [-(y - window.innerHeight / 2) / 2000, (x - window.innerWidth / 2) / 2000, 0.75]
const trans = (x, y, s) => `perspective(800px) rotateX(${x}deg) rotateY(${y}deg) scale(${s})`

function BigButton () {
    const [props, set] = useSpring(() => ({ xys: [0, 0, 1], config: { mass: 5, tension: 350, friction: 70 } }))
    return (
        <animated.div onMouseMove={({ clientX: x, clientY: y }) => set({ xys: calc(x, y) })}
        onMouseLeave={() => set({ xys: [0, 0, 1] })}
        style={{ transform: props.xys.interpolate(trans) }} className='big-button-wrapper'>
            <div className='big-button'>
            
            </div>
        </animated.div>
        
    )
}

export default BigButton;